@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding:wght@400;700&display=swap");

.dd-container {
  width: 100%;
  height: 100vh;
  font-family: "Nanum Gothic Coding", monospace;
}

// header
header {
  margin: 0;
  padding: 0;

  nav {
    padding: 50px;
    align-items: center;
    display: flex;
    justify-content: space-between;
  }

  h3 {
    font-size: 30px;
    font-weight: 700;
    transform: translateX(10%);
    color: #ff8d1a;
  }
}

// section-one
.dd-section-one {
  display: flex;
  height: 70vh;
  justify-content: center;
  align-items: center;
  position: relative;
}

.dd-bg {
  width: 100vw;
  height: 60vh;
  position: absolute;
  background: #fff;
}

.dd-bg-content {
  position: absolute;
  width: 100vw;
  top: 0;
  left: 0;
  transform: translate(5vw, 5vh);
}

.dd-headline {
  font-size: 150px;
  font-weight: 700;
  color: #fff;
  z-index: 3;
}

.dd-bg-img {
  position: absolute;
  left: 0;
  margin-left: 100px;
  margin-top: 70px;
  width: 500px;
  height: 500px;
}

.one {
  animation: imgAni 1s forwards;
  animation-delay: 2s;
}

.two {
  animation: imgAni 2s forwards;
  animation-delay: 3s;
}

.three {
  animation: imgAni 2s forwards;
  animation-delay: 3.5s;
}

.four {
  width: 600px;
  height: 600px;
}

@keyframes imgAni {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.dd-slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to left, #f6f1eb, rgba(#ff8d1a, 0.3));
  z-index: -1;
}

// section-two
.dd-section-two {
  height: 100vh;
  background: linear-gradient(to right, #f6f1eb, #fff);

  .dd-subHeadline {
    margin-top: 20vh;
    margin-left: 10vw;
    margin-bottom: 50px;
    width: 600px;
    font-size: 50px;
    font-weight: 700;
  }

  p {
    margin-left: 11vw;
    font-size: 22px;
    font-weight: 400;
  }

  .dd-highlight {
    width: 500px;
    font-weight: 600;
    color: #ff8d1a;
  }
}

.dd-date-box {
  margin-top: 150px;
  color: #000;
  width: 35vw;
  height: 65vh;
  margin-left: 100px;
  background: rgba(#ff8d1a, 0.2);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(#ff8d1a, 0.1);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;

  .dd-date-start-end {
    padding-top: 20px;
    text-align: left;
    display: flex;
    justify-content: space-between;

    span {
      color: #ff8d1a;
    }
  }

  p {
    padding-left: 10px;
    padding-bottom: 5px;
    margin: 0;
    font-size: 15px;
    font-weight: 400;
    transform: translate(0, 0px);

    span {
      color: #ff8d1a;
    }
  }

  span {
    width: 60px;
    padding: 8px;
    font-size: 20px;
  }
}

// section-three
.dd-section-three {
  background: linear-gradient(to right, #f6f1eb, #fff);
  height: 100vh;
}

.three-left-box {
  padding-left: 10vw;
  padding-top: 20vh;

  h1 {
    font-size: 50px;
    width: 100%;
    font-weight: 700;

    span {
      font-size: 45px;
      color: #ff8d1a;
    }
  }

  p {
    font-size: 22px;
    font-weight: 400;
    padding-top: 10px;
    padding-left: 15px;
  }

  button {
    outline: none;
    cursor: pointer;
    border: none;
    margin-top: 20px;
    margin-left: 15px;
    border-radius: 8px;
    background-color: #ff8d1a;
    width: 180px;
    padding: 10px;
    font-size: 18px;
    color: #fff;
  }
}

.three-right-box {
  width: 40%;
}

.three-img-box {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: left;
  align-items: center;
  background: rgba(#ff8d1a, 0.1);

  img {
    width: 500px;
  }
}

//  section-four
.dd-section-four {
  background: rgba(#ff8d1a, 0.1);
  width: 100%;
  height: 100vh;
  text-align: center;
  position: relative;

  h1 {
    transform: translateY(100px);
    color: #ff8d1a;
    font-weight: 700;
    font-size: 40px;
  }

  span {
    padding-left: 15px;
  }

  button {
    border: none;
    border-radius: 10px;
    outline: none;
    background-color: #ff8d1a;
    color: #fff;
    font-size: 18px;
    padding: 10px 50px;
    transform: translate(0, 150px);
    cursor: pointer;
  }
}

.four-box {
  position: absolute;
  left: 5vw;
  right: 5vw;
  width: 90vw;
  height: 85vh;
  background: rgba(#ff8d1a, 0.1);
  overflow: hidden;
  justify-content: center;

  img {
    width: 500px;
    height: 500px;
    margin-bottom: 50px;
    transform: translate(0px, 250px);
  }
}

@media screen and (max-width: 1200px) {
  // section-one
  .dd-bg-content {
    width: 90vw;
  }

  .dd-headline {
    font-size: 120px;
  }

  .dd-bg-img {
    margin-left: 50px;
    margin-top: 100px;
    width: 500px;
    height: 500px;
  }

  .one {
    width: 400px;
    height: 400px;
  }

  .two {
    width: 400px;
    height: 400px;
  }

  .three {
    width: 400px;
    height: 400px;
  }

  // section-two
  .dd-section-two {
    .dd-subHeadline {
      width: 500px;
      font-size: 42px;
      font-weight: 700;
    }

    p {
      margin-left: 11vw;
      font-size: 20px;
    }

    .dd-highlight {
      width: 450px;
    }
  }

  .dd-date-box {
    margin-top: 150px;
    width: 35vw;
    height: 60vh;
    margin-left: 90px;

    .dd-date-start-end {
      padding-top: 0px;
      padding-bottom: 10px;
      text-align: center;
      display: block;

      span {
        color: #ff8d1a;
      }
    }

    p {
      text-align: center;
      display: block;
      padding-bottom: 5px;
      margin: 0;
      font-size: 14px;
      font-weight: 400;

      span {
        font-size: 14px;
        color: #ff8d1a;
      }
    }

    span {
      width: 50px;
      font-size: 20px;
    }
  }

  // section-three

  .three-left-box {
    padding-left: 8vw;
    padding-top: 18vh;

    h1 {
      font-size: 45px;
      font-weight: 700;

      span {
        font-size: 40px;
      }
    }

    p {
      font-size: 20px;
      font-weight: 400;
      padding-top: 8px;
      padding-left: 13px;
    }

    button {
      margin-top: 15px;
    }
  }

  .three-right-box {
    width: 40%;
  }

  .three-img-box {
    img {
      width: 470px;
    }
  }
}

@media screen and (max-width: 992px) {
  // section-one

  .dd-bg {
    height: 90vh;
    top: 0;
  }

  .dd-bg-content {
    width: 80vw;
  }

  .dd-headline {
    margin-left: 8vw;
    width: 450px;
    font-size: 100px;
  }

  .dd-bg-img {
    margin-left: 10vw;
    margin-top: 20px;
    width: 350px;
    height: 350px;
  }

  .four {
    width: 450px;
    height: 450px;
  }

  // section-two
  .dd-section-two {
    .dd-subHeadline {
      margin-top: 5vh;
      font-size: 42px;
      margin-left: 18vw;
    }

    p {
      margin-left: 20vw;
    }
  }

  .dd-date-box {
    margin-top: 0px;
    width: 55vw;
    height: 60vh;
    margin-left: 17vw;

    .dd-date-start-end {
      padding-top: 10px;

      span {
        color: #ff8d1a;
      }
    }

    p {
      margin: 0;
      font-size: 15px;
      font-weight: 400;

      span {
        color: #ff8d1a;
      }
    }

    span {
      font-size: 18px;
    }
  }

  // section-three

  .three-left-box {
    padding-left: 150px;
    padding-top: 5vh;

    h1 {
      font-size: 45px;

      span {
        font-size: 40px;
      }
    }

    p {
      font-size: 18px;
      font-weight: 400;
      padding-top: 15px;
      padding-left: 15px;
    }

    button {
      position: relative;
      top: 25px;
      margin-top: 20px;
      margin-left: 150px;
      font-size: 18px;
    }
  }

  .three-right-box {
    width: 0vw;
  }

  .three-img-box {
    width: 100vw;
    height: 70vh;
    padding-bottom: 0vh;

    img {
      margin-left: 100px;
      width: 500px;
    }
  }

  //  section-four
  .dd-section-four {
    h1 {
      transform: translateY(100px);
      color: #ff8d1a;
      font-weight: 700;
      font-size: 30px;
    }

    button {
      font-size: 15px;
    }
  }

  .four-box {
    img {
      width: 480px;
      height: 480px;
    }
  }
}

@media screen and (max-width: 768px) {
  // section-one

  .dd-bg-content {
    min-width: 520px;
  }

  .dd-headline {
    margin-left: 0vw;
    width: 400px;
    font-size: 80px;
  }

  .dd-bg-img {
    margin-left: 0vw;
  }

  // section-two
  .dd-section-two {
    .dd-subHeadline {
      margin-top: 5vh;
      font-size: 38px;
      min-width: 520px;
      margin-left: 15vw;
    }

    p {
      margin-left: 17vw;
      font-size: 17px;
    }
    .dd-highlight {
      width: 380px;
      font-weight: 600;
      color: #ff8d1a;
    }
  }

  .dd-date-box {
    margin-top: 0px;
    min-width: 400px;
    margin-left: 15vw;

    p {
      margin: 0;
      font-size: 15px;
      font-weight: 400;
    }

    span {
      font-size: 16px;
    }
  }

  // section-three

  .three-left-box {
    padding-left: 5vw;
    padding-top: 5vh;
    min-width: 500px;
    text-align: left;

    h1 {
      font-size: 40px;
      min-width: 500px;

      span {
        font-size: 35px;
        min-width: 400px;
      }
    }

    p {
      font-size: 18px;
      font-weight: 400;
      padding-top: 15px;
    }

    button {
      position: relative;
      top: 25px;
      margin-left: 0;
      margin-top: 20px;
      font-size: 18px;
    }
  }

  .three-img-box {
    width: 100vw;
    height: 70vh;
    padding-bottom: 0vh;

    img {
      width: 500px;
    }
  }

  //  section-four
  .dd-section-four {
    min-width: 450px;
    h1 {
      transform: translateY(100px);
      color: #ff8d1a;
      font-weight: 700;
      font-size: 27px;
    }

    button {
      font-size: 15px;
    }
  }

  .four-box {
    img {
      width: 480px;
      height: 480px;
    }
  }
}
