.wrapper {
  position: relative;
  width: 45vw;
  min-width: 576px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.container {
  position: relative;
  display: grid;
  padding-left: 50px;
  padding-top: 150px;
  grid-template-columns: repeat(3, minmax(150px, 1fr));
  grid-gap: 10px;
  cursor: pointer;
  will-change: width, height;
}

.item {
  will-change: transform, opacity;
}

.middle {
  position: absolute;
  text-align: center;
  transition: all 1s;
}

.title {
  top: 0;
  left: 0;
  margin-top: 200px;
  color: #fff;
  padding: 20px;
  font-size: 180px;
  font-weight: 700;
  transition: all 0.5s;
  animation-name: titleAnimation;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  margin: 0;
  opacity: 0;
  margin: 2px;
  z-index: 10000;
}

.title:hover {
  color: #000;
}

.title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 50px;
  z-index: -1;
  transition: transform 0.5s;
  transform-origin: 0 0;
  transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
  transform: scaleY(0);
}

.title:hover::before {
  transform: scaleY(1);
}

@keyframes titleAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 1200px) {
  .container {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, minmax(150px, 1fr));
    grid-gap: 10px;
    border-radius: 5px;
    cursor: pointer;
    will-change: width, height;
  }

  .title {
    font-size: 140px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 30px;
    width: 100vw;
    justify-content: left;
  }

  .container {
    padding-top: 50px;
    padding-bottom: 50px;
    grid-template-columns: repeat(4, minmax(150px, 1fr));
  }

  .middle {
    margin: 0;
    padding-left: 120px;
  }

  .title {
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 30px;
    width: 100vw;
    justify-content: left;
  }

  .container {
    padding-top: 50px;
    padding-bottom: 50px;
    grid-template-columns: repeat(3, minmax(150px, 1fr));
  }

  .middle {
    margin: 0;
    padding-left: 50px;
  }

  .title {
    text-align: center;
  }
}
