@import url("https://fonts.googleapis.com/css2?family=Cute+Font&display=swap");

.panda-container {
  font-family: "Cute Font", cursive;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.panda-header-nav {
  background: #fff;
  font-size: 30px;
  min-width: 992px;
  padding-left: 50px;

  .nav-name {
    font-size: 35px;
    padding-right: 20px;
  }
}

.panda-search {
  width: 200px;
  transition: all 1s;
}

.panda-search:focus {
  padding-right: 100px;
}

.panda-bg-box {
  border-radius: 0;
  width: 100%;
  min-width: 992px;
  padding: 60px;
  margin-left: 0;
  overflow: hidden;
  background: #ffb8b8;
  position: relative;
}

.bg-one-box {
  width: 100%;
}

.one-box-content {
  position: relative;
  top: 0;
  left: 0;
  transform: translate(90px, 0px);
  p {
    font-size: 30px;
  }
  h1 {
    font-size: 80px;
    font-weight: 600;
    color: #fff;

    span {
      color: #000;
    }
  }

  .bg-line {
    width: 580px;
    height: 2px;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: #fff;
    opacity: 0.5;
  }
}

.bg-two-box {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;

  img {
    position: absolute;
    width: 550px;
    padding: 50px;
    transform: translate(-150px, -90px);
  }
}

.panda-items {
  width: 80%;
  max-width: 1200px;
  min-width: 992px;
  margin: auto;
  margin-top: 50px;
  padding: 50px;
  -webkit-box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.35);
  background-color: white;
  h3 {
    margin-bottom: 20px;
  }
  h4 {
    color: #1b334d;
    transform: translateY(-10px);
  }

  .box-border {
    position: absolute;
    width: 90%;
    height: 100%;
    z-index: 2;
    border: 0px solid white;
    transition: all 0.5s;
    h4 {
      display: none;
    }
  }

  .box-border:hover {
    border: 15px solid white;
    background-color: rgba(#ffb8b8, 0.7);
    cursor: pointer;
    h4 {
      display: block;
      text-align: center;
      transform: translateY(50px);
      height: 100%;
      color: #fff;
      font-size: 40px;
      font-weight: 600;
    }
  }

  img {
    width: 100%;
    border-radius: 10px;
  }
}

.panda-review {
  width: 80%;
  max-width: 1200px;
  min-width: 992px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: 20px;
  -webkit-box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 2px 5px 1px rgba(0, 0, 0, 0.35);
  background-color: white;
  margin-top: 50px;

  h2 {
    color: #000;
    position: absolute;
    top: 20px;
    left: 40px;
  }
}

.review-container {
  width: 100%;
  margin-top: 50px;
  margin-bottom: 50px;
}

.review {
  width: 350px;
  padding: 30px;
  background-color: rgba(#ffb8b8, 0.2);
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  margin: auto;
  border-radius: 20px;
  box-shadow: 0 15px 50px rgba(#ffb8b8, 0.3);

  .review-content {
    font-size: 22px;
    color: #000;
  }
}

.review-detail {
  display: flex;
  padding-top: 20px;

  img {
    width: 60px;
    border-radius: 50%;
    background-size: cover;
    margin-right: 20px;
  }

  h3 {
    color: #ffb8b8;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 1px;
  }
  span {
    color: #000;
  }
}

.panda-veri {
  width: 80%;
  max-width: 1200px;
  min-width: 992px;
  margin: auto;
  padding-top: 50px;
  margin-bottom: 100px;
  h3 {
    transform: translateY(20px);
    margin: 20px 0px;
  }
  .veri-line {
    width: 80%;
    height: 1px;
    border-top: 2px dotted #ffb8b8;
    transform: translate(100px, 100px);
  }
  .icon-container {
    width: 80%;
    padding: 0;
    transform: translate(50px, 50px);
    display: flex;
    flex-direction: column;
  }
  .veri-icon {
    font-size: 40px;
    width: 110px;
    height: 110px;
    padding-top: 20px;
    text-align: center;
    background-color: white;
    border: 10px solid rgba(#ffb8b8, 0.5);
    border-radius: 50%;
    margin-bottom: 30px;
    -webkit-box-shadow: 0px 10px 58px -8px rgba(#ffb8b8, 0.35);
    -moz-box-shadow: 0px 10px 58px -8px rgba(#ffb8b8, 0.35);
    box-shadow: 0px 10px 58px -8px rgba(#ffb8b8, 0.35);
    transition: all 0.3s;
  }
  h4 {
    padding-top: 20px;
    width: 200px;
    font-size: 30px;
    color: #000;
  }
  p {
    width: 220px;
    font-size: 23px;
    color: #000;
  }
}

.veri-icon:hover {
  background-color: #ffb8b8;
  transform: scale(1.4);
  transition: all 0.7s;
  color: #fff;

  .regi {
    animation-name: left-right;
    animation-duration: 0.7s;
    animation-fill-mode: forwards;
  }
  .check {
    animation-name: hide-show;
    animation-duration: 0.7s;
    animation-fill-mode: forwards;
  }
  .sell {
    animation-name: start;
    animation-duration: 1s;
    animation-fill-mode: forwards;
  }
  .return {
    animation-name: turn;
    animation-duration: 1s;
    animation-fill-mode: forwards;
  }
}

@keyframes left-right {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(-7px);
  }
  50% {
    transform: translateX(7px);
  }
  75% {
    transform: translateX(-7px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes hide-show {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes start {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(60px);
  }
  51% {
    opacity: 0;
  }
  75% {
    opacity: 0;
    transform: translateX(-60px);
  }

  100% {
    transform: translateX(0px);
  }
}

@keyframes turn {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.item-container {
  margin-top: 200px;
  margin-bottom: 100px;
  h1 {
    text-align: center;
    color: #000;
    font-size: 50px;
    margin-bottom: 50px;
  }
  .item {
    box-sizing: border-box;
    margin-top: 30px;
    border: none;
  }
  h5 {
    padding-top: 10px;
    font-size: 28px;
    font-weight: 600;
  }
  h6 {
    font-size: 22px;
    color: #000;

    p {
      color: #ffb8b8;
      font-size: 20px;
      font-weight: 600;
      padding-top: 5px;
    }
  }
}

.luxuryImg {
  width: 100%;
  padding: 5px;
  border-radius: 15px;
}

.panda-footer {
  background: #ffb8b8;
  margin-bottom: 0px;
  padding-bottom: 100px;
  width: 100%;
  min-width: 992px;
  h3 {
    font-size: 40px;
    color: white;
  }
  .footer-icon {
    color: #000;
    background-color: #fff;
    font-size: 30px;
    width: 50px;
    height: 50px;
    padding: 10px;
    border-radius: 50%;
    margin-top: 15px;
    margin-right: 10px;
  }
  .footer-copy {
    color: white;
    margin-top: 15px;

    .copy-icon {
      margin-right: 3px;
    }
  }
  h4 {
    color: white;
    font-size: 35px;
    transform: translateX(55px);
    margin-bottom: 20px;
  }
  .footer-btn {
    width: 80%;
    padding: 10px;
    font-size: 25px;
    display: block;
    margin: auto;
    margin-bottom: 20px;
    background: #ffb8b8;
  }
}

@media screen and (max-width: 1200px) {
  .one-box-content {
    transform: translate(0px, 0px);
    p {
      font-size: 30px;
    }
    h1 {
      font-size: 70px;
    }

    .bg-line {
      width: 500px;
    }
  }

  .bg-two-box {
    img {
      position: absolute;
      width: 530px;
      padding: 50px;
      transform: translate(-180px, -90px);
    }
  }

  .review-container {
    display: flex;
    min-width: 780px;
  }

  .review {
    width: 280px;
    padding: 30px;
    background-color: rgba(#ffb8b8, 0.2);
    padding-top: 50px;
    padding-bottom: 50px;
    display: flex;
    margin: auto;
    border-radius: 20px;
    box-shadow: 0 15px 50px rgba(#ffb8b8, 0.3);

    .review-content {
      font-size: 20px;
      color: #000;
    }
  }

  .review-detail {
    display: flex;
    padding-top: 20px;

    img {
      width: 50px;
      border-radius: 50%;
      background-size: cover;
      margin-right: 20px;
    }

    h3 {
      color: #ffb8b8;
      font-size: 18px;
      font-weight: 400;
      letter-spacing: 1px;
    }
    span {
      color: #000;
    }
  }

  .panda-veri {
    .veri-line {
      width: 80%;
      height: 1px;
      border-top: 2px dotted #ffb8b8;
      transform: translate(20px, 100px);
    }

    .icon-container {
      width: 60%;
      padding: 0;
      transform: translate(0px, 50px);
      display: flex;
      flex-direction: column;
    }

    .veri-icon {
      font-size: 35px;
      width: 100px;
      height: 100px;
      padding-top: 18px;
      border: 9px solid rgba(#ffb8b8, 0.5);
    }
    h4 {
      padding-top: 12px;
      width: 150px;
      font-size: 28px;
      color: #000;
    }
    p {
      width: 150px;
      font-size: 22px;
      color: #000;
    }
  }
}
