@import url("https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap");

html,
body {
  background-color: #fff;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  padding: 10px 50px;
  width: 100vw;
  min-width: 576px;
  background-color: transparent;
  position: fixed;
  z-index: 7;
  transition: all 0.5s;
  font-family: "Do Hyeon", sans-serif;
}

.headerAdd {
  background-color: rgba(#2d98da, 0.8);
  border-bottom: 1px solid lightslategray;
}

.header-title {
  font-size: 35px;
}
.header-item {
  text-align: center;
}

.header-item {
  font-size: 20px;
  margin-right: 20px;
}

.app-mobile {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 550px;
  height: 100vh;
  background: linear-gradient(to top, #2f3640, #fd7272, #fd9644, #2d98da);
}

.mobile {
  position: relative;
  top: -70px;
  width: 300px;
  height: 480px;
  background: #020c17;

  .top {
    position: absolute;
    top: -50px;
    width: 100%;
    height: 50px;
    background: #efefef;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
  }

  .bottom {
    position: absolute;
    width: 100%;
    bottom: -50px;
    height: 50px;
    background: #efefef;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
  }
}

.top::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 35%;
  transform: translateY(-50%);
  width: 4px;
  height: 4px;
  background: #ccc;
  border: 3px solid #aaa;
  border-radius: 50%;
  box-shadow: 0 0 0 1px #999;
}

.top::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 3px;
  background: #ccc;
  border: 3px solid #aaa;
  border-radius: 3px;
}

.bottom::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
  height: 15px;
  border: 2px solid #999;
  cursor: pointer;
  border-radius: 6px;
}

.screen {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: mobileAni 0.5s;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: 0.5s;
  }
}

.screenActive {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  animation: mobileAni 0.5s;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: 0.5s;
    z-index: 0;
  }

  .clock {
    color: #fff;
    z-index: 1;
    transform: translateY(40px);
    opacity: 0;
    animation-name: mobileAniClock;
    animation-duration: 1s;
    animation-delay: 0.5s;
    animation-fill-mode: forwards;

    h2 {
      font-size: 57px;
      line-height: 35px;
      text-shadow: 0 1px 3px rgba(255, 255, 255, 0.5);
    }

    span {
      font-size: 20px;
    }
  }

  ul {
    position: absolute;
    margin: 0;
    padding: 0;
    bottom: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 50px;

    li {
      list-style: none;
      width: 50px;
      height: 50px;
      margin-bottom: 7px;
      text-align: center;
      color: #fff;
      font-size: 35px;
      opacity: 0;
      transition: all 0.5s;
      animation: mobileAniIcons 1s forwards;
      animation-delay: 0.5s;
      z-index: 2;
      cursor: pointer;
    }
  }
}

.mobilePhone {
  background: linear-gradient(to right, #2ecc71, #27ae60);
  border-radius: 8px;
  padding: 7px;
  transition: all 0.5s;
}

.mobileComment {
  background: linear-gradient(to bottom, #3742fa, #1e90ff);
  border-radius: 8px;
  padding: 7px;
  transition: all 0.5s;
}

.mobileImage {
  background: linear-gradient(to left, #f9ca24, #f6e58d);
  border-radius: 8px;
  padding: 7px;
  color: #fff;
  transition: all 0.5s;
}

.mobileCamera {
  background: linear-gradient(to right, #ff6b6b, #ee5253);
  border-radius: 8px;
  padding: 7px;
  transition: all 0.5s;
}

.mobilePhone:hover,
.mobileComment:hover,
.mobileImage:hover,
.mobileCamera:hover {
  transform: translateY(-12px);
}

@keyframes mobileAni {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes mobileAniClock {
  0% {
    opacity: 0;
    transform: translateY(80px);
  }
  100% {
    opacity: 1;
  }
}

@keyframes mobileAniIcons {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
  }
}

.screen::before {
  content: "";
  position: absolute;
  top: 0;
  left: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  z-index: 1;
  transform: skewX(-5deg);
}

.screenActive::before {
  content: "";
  position: absolute;
  top: 0;
  left: -50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  z-index: 1;
  transform: skewX(-5deg);
}

.cloud0 {
  --i: 1;
}

.cloud1 {
  --i: 2;
}

.cloud2 {
  --i: 3;
}

.cloud3 {
  --i: 4;
}

.cloud4 {
  --i: 5;
}

.cloud5 {
  --i: 10;
}

.cloud6 {
  --i: 9;
}

.cloud7 {
  --i: 8;
}

.cloud8 {
  --i: 7;
}

.cloud9 {
  --i: 6;
}

.clouds {
  position: absolute;
  top: 170px;
  left: 0;
  width: 100%;
  min-width: 550px;
  height: 100%;
  overflow: hidden;
  pointer-events: none;

  img {
    position: absolute;
    bottom: 0px;
    max-width: 100%;
    animation: cloudAni calc(3s * var(--i)) linear infinite;
  }
}

@keyframes cloudAni {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: scale(3);
  }
}

.skills {
  width: 100%;
  min-width: 576px;
  height: 1500px;
  position: relative;
  overflow: hidden;
}

.skillPhone {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 6;
  padding-bottom: 50px;

  div {
    transform: translate(-40px, -30px);
  }

  img {
    width: 650px;
    padding-top: 0px;
    transform: translateX(-20px);
    z-index: 6;
  }
}

.skillBg1 {
  width: 100%;
  height: 100%;
  background: none;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100vw);
  background: #f1f2f6;
  margin-top: 100px;
}

.skillBg2 {
  width: 100%;
  height: 100%;
  background: #000;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100vw);
  margin-top: 100px;
}

.skillBg3 {
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 1;
  margin-top: 100px;
}

.portfolio {
  position: relative;
  top: 100px;
  width: 100%;
  min-width: 576px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: "Do Hyeon", sans-serif;

  .portOne {
    width: 100%;
    height: 100vh;
    background-color: #ffb8b8;
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
      color: white;
      font-size: 120px;
      padding-top: 200px;
      padding-bottom: 50px;
    }

    p {
      color: #000;
      font-size: 28px;
      padding-left: 50px;
    }
  }

  .oneHover {
    margin-bottom: 100px;
  }

  .portTwo {
    width: 100%;
    height: 100vh;
    background-color: #f6f1eb;
    text-align: left;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
      color: #000;
      font-size: 120px;
      padding-top: 200px;
      padding-bottom: 50px;
      text-align: center;
    }

    p {
      color: #936540;
      font-size: 28px;
      padding-left: 50px;
      text-align: center;
    }
  }

  .twoHover {
    padding-top: 150px;
  }
}

.port-hoverOne {
  cursor: pointer;
  transform: translateY(-250px);
}

.port-hoverTwo {
  cursor: pointer;
  transform: translateY(-200px);
}

@media screen and (max-width: 1200px) {
  .clouds {
    top: 100px;
  }

  .skillPhone {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 6;
    padding-bottom: 50px;

    div {
      transform: translate(-30px, -30px);
    }

    img {
      width: 500px;
      padding-top: 0px;
      transform: translateX(-20px);
      z-index: 6;
    }
  }

  .portOne {
    h1 {
      font-size: 100px;
      padding-top: 200px;
      padding-bottom: 50px;
    }

    p {
      color: #000;
      font-size: 26px;
      padding-left: 50px;
    }
  }

  .portfolio {
    .portOne {
      h1 {
        font-size: 110px;
      }

      p {
        font-size: 26px;
      }
    }

    .portTwo {
      h1 {
        font-size: 110px;
      }

      p {
        font-size: 26px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header-items {
    background-color: rgba(255, 255, 255, 0.2);
    width: 100px;
  }

  .header-item {
    text-align: right;
  }

  .clouds {
    top: 80px;
  }

  .skillPhone {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    z-index: 6;
    padding-bottom: 50px;

    div {
      transform: translate(0px, 0px);
    }

    img {
      padding-left: 0px;
    }
  }

  .portfolio {
    .portOne {
      h1 {
        font-size: 90px;
      }

      p {
        font-size: 22px;
        padding-left: 30px;
      }
    }

    .portTwo {
      h1 {
        font-size: 90px;
      }

      p {
        font-size: 22px;
      }
    }
  }

  .port-hoverOne {
    cursor: pointer;
    transform: translateY(-300px);
  }

  .port-hoverTwo {
    cursor: pointer;
    transform: translateY(-300px);
  }
}

@media screen and (max-width: 768px) {
  .header-items {
    width: 88px;
  }

  .header-item {
    font-size: 18px;
    text-align: right;
  }

  .clouds {
    top: 30px;
  }

  .skillPhone {
    img {
      padding-left: 50px;
    }
  }

  .portfolio {
    .portOne {
      h1 {
        font-size: 70px;
      }

      p {
        font-size: 18px;
        padding-left: 0px;
      }
    }

    .portTwo {
      h1 {
        font-size: 70px;
      }

      p {
        font-size: 18px;
      }
    }
  }

  .port-hoverOne {
    cursor: pointer;
    transform: translate(-20px, -350px);
  }

  .port-hoverTwo {
    cursor: pointer;
    transform: translate(20px, -350px);
  }
}
